<template>
	<view class="container">
		<view class="header">
			<image :src="userinfo.head_portrait" mode="scaleToFill" class="avatar" v-if="vuex_login"></image>
			<image src="../../static/images/avatar.png" mode="scaleToFill" class="avatar" v-else></image>
			<view class="info" v-if="vuex_login">
				<view class="name">{{userinfo.nickname}}</view>
				<view class="desc">邀请码：{{userinfo ? userinfo.promo_code:''}}</view>
			</view>
			<view class="info" v-else>
				<navigator class="name" hover-class="none" url="../login/index">登录 / 注册</navigator>
				<view class="desc"></view>
			</view>
		</view>
		<view class="cell-box">
			<view class="cell">
				<view class="label">手机号码</view>
				<view class="right">
					{{userinfo.mobile}}
				</view>
			</view>
			<navigator class="cell" hover-class="none" url="./xieyi">
				<view class="label">用户协议</view>
				<view class="right">
					<uni-icons type="arrowright" size="14"></uni-icons>
				</view>
			</navigator>
			<navigator class="cell" hover-class="none" url="./yinsi">
				<view class="label">隐私协议</view>
				<view class="right">
					<uni-icons type="arrowright" size="14"></uni-icons>
				</view>
			</navigator>

		</view>
		<view class="add" @click="logout">
			退出登录
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userinfo: {
					nickname:'',
					promo_code:'',
					mobile:''
				}
			}
		},
		onLoad() {
			if (this.vuex_login == 0) {
				this.$u.route('pages/login/index');
				return
			}
		},
		onShow() {
			this.getUserInfo()
		},
		methods: {
			async getUserInfo() {
				let res = await this.$u.get('/v2/member/member/view', {
					id: this.vuex_user.id
				})
				this.userinfo = res.data
			},
			logout(){
				this.$u.vuex('vuex_login',0)
				uni.switchTab({
					url:'/pages/index/index'
				})
			},
			showService() {
				this.$refs.popup.open('top')
			}
		}
	}
</script>
<style>
	page {
		background-color: #F5F5F5;
	}
</style>
<style scoped>
	.container {
		 
		min-height: 100vh;
	}

	.header {
		display: flex;
		background-color: #fff;
		height: 160rpx;
		color: #000;
		align-items: center;
		margin-top: 20rpx;
	}

	.header .avatar {
		width: 100rpx;
		height: 100rpx;
		border-radius: 50%;
		margin: 0 30rpx 0 30rpx;
	}

	.header .info .name {
		font-size: 32rpx;
		margin-bottom: 10rpx;
		font-weight: bold;
	}
	.header .info .desc {
		font-size: 24rpx;
		color: #555
	}

	.cell-box {
		margin: 20rpx 0 0 0;
		position: relative;
		background-color: #fff;
		padding: 0 20rpx;

	}

	.cell-box .cell {
		border-bottom: #EDEDED 1px solid;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 30rpx 20rpx;

	}

	.cell-box .cell.center {
		justify-content: center;
	}

	.cell-box .cell:last-child {
		border-bottom: none;
	}

	.cell-box .label {
		font-size: 28rpx;
		color: #333;
	}

	.cell-box .right {
		margin-left: 30rpx;
		text-align: right;
		color: #999999;
		font-size: 14px;
	}

	.cell-box .right .time {
		font-size: 14px;
	}
	.add {
		margin: 40rpx 20rpx;
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		height: 88rpx;
		border-radius: 44rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #FFF;
		width: 690rpx;
	}
</style>
